import React from 'react';
import PropTypes from 'prop-types';

/**
 * Results heading, summarising the response.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__summary(props) {
    let searchHints;
    const result =
        props.totalMatching === 1
            ? props.summariseAs.singular
            : props.summariseAs.plural;

    if (props.totalMatching == 0) {
        searchHints = (
            <div>
                <p>Your search did not match any {props.summariseAs.plural}.</p>
                <p>Suggestions:</p>
                <ul>
                    <li>Make sure that all words are spelled correctly</li>
                    <li>Try different keywords</li>
                    <li>Try more general keywords</li>
                    <li>Try fewer keywords</li>
                    <li>Try fewer filters</li>
                    <li>
                        <button
                            type="button"
                            onClick={() => {
                                props.clear();
                            }}
                        >
                            Reset filters
                        </button>
                    </li>
                    <li>
                        <button
                            type="button"
                            onClick={() => {
                                const newQuery = props.query;
                                newQuery.query = '';
                                newQuery.sortBy = props.config.sort;
                                props.update.query(newQuery);
                                props.update.results(!props.update.updateState);
                            }}
                        >
                            Reset search
                        </button>
                    </li>
                </ul>
            </div>
        );
    }

    return (
        <div className="finder__results__summary">
            <h2
                aria-live="polite"
                className="finder__results__summary__heading"
            >
                {props.query.query || Object.keys(props.query.facets).length > 0
                    ? 'Matching'
                    : 'All'}{' '}
                {result} (showing{' '}
                {props.totalMatching > props.numRanks && (
                    <>
                        <span>{props.currStart}</span>
                        <span>{props.currEnd}</span> of{' '}
                    </>
                )}
                <span>{props.totalMatching}</span> {result}
                {props.query.query && ` for ${props.query.query}`})
            </h2>
            {searchHints}
        </div>
    );
}

finder__results__summary.propTypes = {
    clear: PropTypes.func,
    config: PropTypes.object,
    currEnd: PropTypes.number,
    currStart: PropTypes.number,
    numRanks: PropTypes.number,
    query: PropTypes.object,
    summariseAs: PropTypes.object,
    totalMatching: PropTypes.number,
    update: PropTypes.object,
};

export default finder__results__summary;
